import React,{useEffect,useState} from 'react';
import {Data} from '../data.js';
import {Languages} from '../data.js';

const totalQ=9;
export const Guess=()=>{

    const [dataLanguage,setDataLanguage]=useState([]);
    const [options,setOptions]=useState([]);
    const [correctAnswer, setCorrectAnswer]=useState("");
    const [goodAnswers, setGoodAnswers]=useState(0);
    const [start,setStart]=useState(false);
    const [end,setEnd]=useState(false);
    const [nextQ, setNextQ]=useState(false);
    const [numQ, setNumQ]=useState(0);

    useEffect(()=>{
        getInfoLanguage()
    },[]);

    useEffect(()=>{
        if(numQ > totalQ){
            setTimeout(()=>{
                setStart(prevState=>!prevState);
                setEnd(prevState=>!prevState);
            },1500);
            
        }else if(numQ > 0 && numQ <= totalQ){
            setTimeout(()=>{
                setNextQ(prevState=>!prevState);
                getInfoLanguage();
            },1500);
        }
    },[numQ]);

    function getInfoLanguage(){
        let arrayLanguages=getRandomLanguages(); //["java","c++","c","php"]
        let selectedLanguage=arrayLanguages[getRandomArbitrary(0, arrayLanguages.length)];  //elegir aleatoriamente un lenguaje del array 
        
        let infoLanguage=Data[selectedLanguage.toString()]; //buscar en objeto
        //obtener del array un dato aleatorio "java":["","",""] 
        let infoLanguageIndex=infoLanguage[getRandomArbitrary(0,infoLanguage.length)];
        
        setOptions(arrayLanguages); //array de opciones
        setDataLanguage(infoLanguageIndex); //informacion del lenguaje
        setCorrectAnswer(selectedLanguage); //lenguaje seleccionado, la respuesta correcta  
    }

    // Retorna un número aleatorio entre min (incluido) y max (excluido)
    function getRandomArbitrary(min, max) {
            return Math.trunc(Math.random() * (max - min)) + min;
    }

    function getRandomLanguages(){
        let languages=[];
        let randomCompleted=false;

        //si es false sigue corriendo
        while(!randomCompleted){
            //obtener un language[entre 0 y la longitud del array]
            let language=Languages[getRandomArbitrary(0,Languages.length)]

            //si el array languages no contiene el language lo agrega al array
            //includes devuelve true o false 
            if(!languages.includes(language)){
                    languages.push(language);
            }
            //cuando el array sea longitud 4 acaba el bucle
            if(languages.length === 4){
                randomCompleted=true;
            }
        }
        return languages;
    }

    function verifyAnswer(option=''){
        setNextQ(prevState=>!prevState);
        setNumQ(numQ+1);

        if(correctAnswer === option){
            setGoodAnswers(goodAnswers+1);
        }
    }

        
    if(end){
        return(
            <div className="app  info">
                <h1 className="title">Terminado!</h1>
                <p className="parag">Tu Puntuacion es: {goodAnswers} / {numQ}</p>
                <button className="primary-btn" onClick={
                            ()=>{
                                window.location.reload();
                            }
                        }>Regresar a inicio</button>
            </div>
        );
    }

    
    if(start){
            return(
                <div className="app">
                    
                    <div className="screen">
                        <p className="languageInfo">{dataLanguage}</p>
                    </div>
                    
                    <div className="options">
                    {
                        options.map((op,i)=>{
                            return (
                                <div key={i}>
                                    <button
                                    style={{
                                        border:(!nextQ)? '' : (correctAnswer === op) ? '2px solid green':'2px solid red',
                                        opacity:(!nextQ)? '' : (correctAnswer === op) ? '':'.4'
                                    }}
                                    className="btn btn-primary bg-primary"
                                    disabled={(nextQ) ? true : false}
                                    onClick={()=>verifyAnswer(op)}>{op}</button>
                                </div>
                            )
                        })
                    }
                    </div>
                    <div style={{textAlign:'center'}}>
                        <button className="primary-btn" onClick={()=>{
                                window.location.reload();
                        }
                        }>Salir</button>
                    </div>
                </div>
            );
    }

    
    return(
        <div className="app  info">
                    <h1 className="title">Bienvenidx</h1>
                    <p className="parag">Juega y descubre que tanto sabes sobre los lenguajes de programacion.</p>
                    <button className="primary-btn" onClick={
                        ()=>{
                            setStart(prevState=>!prevState)
                        }
                    }>Empezar a Jugar</button>
        </div>

    );
}